$(document).ready(function(){
    $(".fixed__menu").on("click","a", function (event) {
        event.preventDefault();
        var id  = $(this).attr('href'),
            top = $(id).offset().top;
        $('body,html').animate({scrollTop: top -55}, 1500);
    });
    $(".fixed__logo").on("click","a", function (event) {
        event.preventDefault();
        var id  = $(this).attr('href'),
            top = $(id).offset().top;
        $('body,html').animate({scrollTop: top -55}, 1500);
    });
    $(".footer__menu").on("click","a", function (event) {
        event.preventDefault();
        var id  = $(this).attr('href'),
            top = $(id).offset().top;
        $('body,html').animate({scrollTop: top -55}, 1500);
    });
    $(".header").on("click","button", function (event) {
        event.preventDefault();
        var id  = $(this).attr('href'),
            top = $(id).offset().top;
        $('body,html').animate({scrollTop: top -55}, 1500);
    });
    $("#company").on("click",".company__btn", function openDetails(event) {
            $(".details").addClass(".details--active");
    });

    $(document).on(`click`, `.fixed__humburger`, openMenu);
    $(document).on(`click`, `.main-menu__close`, closeMenu);
    
    function openMenu(event) {
		$(`.main-menu`).addClass(`main-menu--active`);
	}
	function closeMenu(event) {
		$(`.main-menu`).removeClass(`main-menu--active`);
	}
});

    
